import { ValuesModel } from '@/checkout/data/models';
import { events } from '@adobe-commerce/event-bus';

export const initialValues: ValuesModel = {
  email: '',
  isBillToShipping: true,
  selectedPaymentMethod: null,
  selectedShippingMethod: null,
};

/**
 * Emits a checkout/values event with the provided values
 * Merges with previous values if they exist
 *
 * @param values - Partial values to be emitted or merged with existing values
 */
export function notifyValues(values: Partial<ValuesModel>): void {
  const prev = events.lastPayload('checkout/values') ?? initialValues;
  const next = { ...prev, ...values };
  events.emit('checkout/values', next);
}

/**
 * Retrieves a specific value from the ValuesModel by key
 * @param key - The key of the ValuesModel to retrieve
 * @returns The value for the specified key or null if not defined/found
 */
export function getValue<K extends keyof ValuesModel>(
  key: K
): ValuesModel[K] | null {
  const values = events.lastPayload('checkout/values');
  return values && key in values ? values[key] : null;
}
